<template>
<div class="add">
    <van-nav-bar fixed title="添加地址" left-arrow @click-left="$router.go(-1)" />

    <form action="">
        <div class="username">
            <label for="username">姓名</label>
            <input type="text" name="" id="username" placeholder="收件人姓名">
        </div>
        <div class="phone">
            <label for="phone">电话</label>
            <input type="text" name="" id="phone" placeholder="收件人手机号">
        </div>
        <div class="select-area">
            <label for="area">地区</label>
            <input type="text" name="" id="area" placeholder="请选择地区"  @click="showPopup">
            <van-popup v-model="show" position="bottom" :style="{ height: '40%' }">
                <select name="province" id="" value="0">
                    <option value="0">请选择省份</option>
                    <option value="">123</option>
                </select>
                <select name="city" id="" value="0">
                    <option value="0">请选择城市</option>
                    <option value="">123</option>
                </select>
                <select name="area" id="" value="0">
                    <option value="0">请选择地区</option>
                    <option value="">123</option>
                </select>
            </van-popup>
        </div>
        <div class="details">
            <label for="details">详细地址</label>
            <input type="text" name="" id="details" placeholder="街道门牌、楼层房间号等信息">
        </div>

        <div class="setDefault">设为默认收货地址 <van-switch v-model="checked" /></div>

    </form>
</div>
</template>

<script>
import { getArea } from '@/api/address'
export default {
  name: 'addressManage',
  data () {
    return {
      show: false,
      checked: false,
      areaList: {}
    }
  },
  methods: {
    showPopup () {
      this.show = true
    },
    async getArea () {
      const res = await getArea()
      this.areaList = res.data.list
      //   this.areaList.map(e=> e[1].city[2].name)
      console.log(res)
    }
  },
  created () {
    this.getArea()
  }
}
</script>

<style lang="less" scoped>
.add {
  padding-top: 46px;
  padding-bottom: 46px;
  ::v-deep {
    .van-nav-bar__arrow {
      color: #333;
    }
  }
}
</style>
